.content {
  margin-top: 62px;
}

.main-wrapper {
  @media (max-width: $desk) {
    overflow: hidden;
  }
}

.home-main-section {
  position: relative;
  padding: 57px 0;

  @media (max-width: $tablet) {
    padding: 37px 0 224px;
  }

  @media (max-width: $mobile) {
    padding: 28px 0 220px;
  }

  .wrapper {
    display: flex;
    align-items: center;

    @media (max-width: $mobile) {
      display: block;
    }

    .left-block {
      position: relative;
      flex: 0 682px;
      width: 682px;
      margin-right: 47px;
      z-index: 1;

      @media (max-width: $mobile) {
        width: 100%;
        margin: 0;
      }

      h1,
      .h1 {
        margin-bottom: 12px;

        @media (max-width: $mobile) {
          margin-bottom: 3px;
        }

        i {
          display: block;
        }
      }

      p {
        margin: 0 0 44px;
      }

      .search-form {
        position: relative;

        @media (max-width: $tablet) {
          max-width: 568px;
        }

        .form-control {
          border-radius: 12px;
          border: 1px solid #E2E6E7;
          background: #FFF url(../img/search-md.svg) 20px 50% no-repeat;
          box-shadow: 0px 4px 6px -2px rgba(16, 24, 40, 0.03), 0px 12px 16px -4px rgba(16, 24, 40, 0.04);
          padding-left: 53px;
          padding-right: 130px;
          height: 54px;

          @media (max-width: $mobile) {
            height: 44px;
            padding-left: 44px;
            background-position: 12px 50%;
            padding-right: 100px;
          }
        }

        .btn {
          position: absolute;
          top: 5px;
          right: 5px;
          width: 120px;
          border-radius: 10px;

          @media (max-width: $mobile) {
            width: 92px;
            height: 34px;
          }
        }
      }
    }

    .right-block {
      @media (max-width: $tablet) {
        position: absolute;
        top: 164px;
        right: -83px;
        z-index: 0;
      }

      @media (max-width: $mobile) {
        top: auto;
        bottom: 11px;
        right: auto;
        left: 50%;
        margin-left: -50.5px;
      }

      img {
        @media (max-width: $tablet) {
          width: 287px;
          height: 390px;
          object-fit: cover;
        }

        @media (max-width: $mobile) {
          width: 159px;
          height: 190px;
        }
      }
    }
  }
}

.about-us-section {
  border-top: 1px solid #e8e8e8;

  @media (max-width: $tablet) {
    padding: 42px 0 35px;
  }

  @media (max-width: $mobile) {
    padding: 80px 0 50px;
  }

  .wrapper {
    max-width: 940px;
    margin: 0 auto;
    display: flex;
    align-items: center;

    @media (max-width: $tablet) {
      max-width: 670px;
    }

    @media (max-width: $mobile) {
      flex-wrap: wrap;
    }

    .right-block {
      flex: 0 612px;
      width: 612px;
      margin-left: 75px;

      @media (max-width: $tablet) {
        flex: 0 435px;
        width: 435px;
        margin-left: 0;
      }

      @media (max-width: $mobile) {
        order: 1;
        width: 100%;
      }

      h2,
      .h2 {
        margin-bottom: 24px;
      }

      p {
        margin: 14px 0 0;

        &:first-of-type {
          margin-top: 0;
        }

        &:last-of-type {
          margin-top: 24px;
        }
      }

      .btn {
        width: 138px;
        margin-top: 26px;
      }
    }

    .left-block {
      @media (max-width: $mobile) {
        order: 2;
        width: 100%;
        display: flex;
        justify-content: center;
      }

      img {
        @media (max-width: $mobile) {
          width: 235px;
        }
      }
    }
  }
}

.our-mission-section {
  @media (max-width: $tablet) {
    padding: 35px 0 0;
  }

  @media (max-width: $mobile) {
    padding: 50px 0 0;
  }

  .wrapper {
    max-width: 940px;
    margin: 0 auto;
    display: flex;
    align-items: center;

    @media (max-width: $tablet) {
      max-width: 670px;
      justify-content: space-between;
    }

    @media (max-width: $mobile) {
      display: block;
    }

    .left-block {
      flex: 0 612px;
      width: 612px;
      margin-right: 70px;

      @media (max-width: $tablet) {
        flex: 0 385px;
        width: 385px;
        margin-right: 0;
      }

      @media (max-width: $mobile) {
        width: 100%;
      }

      h2,
      .h2 {
        margin-bottom: 32px;
      }

      p {
        margin: 14px 0 0;

        &:first-of-type {
          margin-top: 0;
        }

        &:last-of-type {
          margin-top: 24px;
        }
      }

      .btn {
        width: 180px;
        margin-top: 24px;
      }
    }

    .right-block {
      @media (max-width: $tablet) {
        flex: 1 0 auto;
        display: flex;
        justify-content: flex-end;
      }

      @media (max-width: $mobile) {
        width: 100%;
        justify-content: center;
        margin-top: 20px;
      }

      img {
        @media (max-width: $tablet) {
          width: 240px;
        }

        @media (max-width: $mobile) {
          width: 271px;
        }
      }
    }
  }
}

.main-cta {
  max-width: 1200px;
  padding: 40px 0 40px;
  margin: 60px auto 0;
  background: url(../img/desk-cta.jpg) 50% 50% no-repeat;
  border-radius: 8px;

  @media (max-width: $tablet) {
    margin: 60px 24px 0;
    background: url(../img/tablet-cta.jpg) 50% 50% no-repeat;
    padding: 23px 25px;
    background-size: auto 100%;
  }

  @media (max-width: $mobile) {
    margin: 100px 0 0;
    border-radius: 0;
    padding: 29px 16px;
    background-size: cover;
  }

  .container {
    @media (max-width: $tablet) {
      padding: 0;
    }
  }

  .cta-text {
    display: block;
    max-width: 940px;
    margin: 0 auto 24px;
    font-size: 24px;
    font-weight: 500;
    line-height: 140%;
    color: #fff;
    text-align: center;

    @media (max-width: $mobile) {
      font-size: 16px;
    }
  }

  .btn {
    width: 230px;
    height: 52px;
    display: flex;
    margin: 0 auto;

    @media (max-width: $mobile) {
      width: 210px;
      height: 44px;
      font-size: 16px;
    }
  }
}

.collaboration-section {
  padding: 80px 0;

  @media (max-width: $mobile) {
    padding: 100px 0;
  }

  .wrapper {
    display: flex;
    align-items: flex-start;

    @media (max-width: $tablet) {
      align-items: flex-end;
      justify-content: space-between;
    }

    @media (max-width: $mobile) {
      flex-wrap: wrap;
    }

    .right-block {
      flex: 0 690px;
      width: 690px;
      margin-left: 48px;

      @media (max-width: $tablet) {
        flex: 0 410px;
        width: 410px;
        margin-left: 23px;
      }

      @media (max-width: $mobile) {
        order: 1;
        width: 100%;
        flex: 0 100%;
        margin-left: 0;
      }

      h2,
      .h2 {
        margin-bottom: 24px;

        @media (max-width: $tablet) {
          margin-left: -310px;
        }

        @media (max-width: $mobile) {
          margin-left: 0;
        }
      }

      p {
        margin: 0 0 18px;

        @media (max-width: $tablet) {
          margin-left: -310px;
        }

        @media (max-width: $mobile) {
          margin-left: 0;
          margin-bottom: 38px;
        }
      }

      .form-wrapper {
        display: flex;
        flex-wrap: wrap;
        margin: -6px -15px;

        @media (max-width: $tablet) {
          margin: -6px 0;
        }

        .f_group {
          flex: 0 calc(100% - 30px);
          width: calc(100% - 30px);
          margin: 6px 15px;

          @media (max-width: $tablet) {
            flex: 0 100%;
            width: 100%;
            margin: 6px 0;
          }

          &.semi {
            flex: 0 calc(50% - 30px);
            width: calc(50% - 30px);
            margin: 6px 15px;

            @media (max-width: $tablet) {
              flex: 0 100%;
              width: 100%;
              margin: 6px 0;

            }
          }

          &.f_group_upload {
            margin-top: 14px;
            margin-bottom: 14px;
          }
        }

        .nano {
          height: 85px;
        }

        .btn {
          width: 165px;
          float: right;
        }
      }
    }

    .left-block {
      @media (max-width: $tablet) {
        flex: 0 236px;
        width: 236px;
      }

      @media (max-width: $mobile) {
        flex: 0 100%;
        width: 100%;
        order: 2;
        margin-top: 27px;
        display: flex;
        justify-content: center;
      }

      img {
        @media (max-width: $tablet) {
          height: 396px;
          object-fit: cover;
        }

        @media (max-width: $mobile) {
          height: 374px;
          width: 270px;
        }
      }
    }
  }
}

.copyright-section {
  position: relative;
  padding-bottom: 80px;

  @media (max-width: $tablet) {
    padding-bottom: 100px;
  }

  .wrapper {
    max-width: 1070px;
    display: flex;
    align-items: flex-start;

    @media (max-width: $tablet) {
      max-width: 670px;
      margin: 0 auto;
    }

    @media (max-width: $mobile) {
      display: block;
    }

    .left-block {
      flex: 0 500px;
      width: 500px;

      @media (max-width: $tablet) {
        flex: 0 297px;
        width: 297px;
      }

      @media (max-width: $mobile) {
        width: 100%;
      }

      h2,
      .h2 {
        padding-left: 120px;
        margin-bottom: 6px;
        text-align: left;

        @media (max-width: $tablet) {
          padding-left: 0;
          margin-bottom: 11px;
        }

        @media (max-width: $mobile) {
          margin-bottom: 30px;
        }
      }

      img {
        display: inline-block;
        margin-left: 120px;

        @media (max-width: $tablet) {
          margin-left: 0;
          width: 248px;
          height: 140px;
        }

        @media (max-width: $mobile) {
          display: block;
          width: auto;
          height: 130px;
          margin: 0 auto;
        }
      }
    }

    .right-block {
      flex: 0 calc(100% - 500px - 32px);
      width: calc(100% - 500px - 32px);
      margin-left: 32px;

      @media (max-width: $tablet) {
        flex: 0 calc(100% - 297px - 10px);
        width: calc(100% - 297px - 10px);
        margin-left: 10px;
      }

      @media (max-width: $mobile) {
        width: 100%;
        margin: 12px 0 0;
      }

      p {
        margin: 0;
        padding: 16px 0;
        border-top: 1px dashed #e1e6f0;

        &:first-child {
          padding-top: 0;
          border-top: 0;
        }

        &:last-of-type {
          padding-bottom: 0;
        }
      }
    }
  }
}

.shadehouse-section {
  position: relative;
  padding: 60px 0 100px;
  overflow: hidden;

  h2,
  .h2 {
    text-align: center;
    margin-bottom: 19px;
  }

  .top-container {
    position: relative;
    padding-top: 32px;
    padding-bottom: 25px;

    &:before {
      position: absolute;
      top: 0;
      left: -1500px;
      right: 24px;
      content: "";
      border-top: 1px solid #E8E8E8;

      @media (max-width: $tablet) {
        right: 50px;
      }

      @media (max-width: $mobile) {
        right: 16px;
      }
    }

    .content-wrapper {
      display: flex;
      align-items: center;
      max-width: 940px;
      margin: 0 auto;

      @media (max-width: $tablet) {
        display: block;
        max-width: 668px;
      }

      .left-block {
        flex: 1 0 auto;

        @media (max-width: $tablet) {
          display: none;
        }

        img {
          width: 294px;
          height: 443px;
        }
      }

      .right-block {
        flex: 0 612px;
        width: 612px;
        margin-left: 34px;

        @media (max-width: $tablet) {
          width: 100%;
          margin: 0;
        }

        p {
          margin: 0;

          &:first-child {
            &:first-letter {
              initial-letter: 3;
              font-size: 75px;
              font-weight: $font-wix;
              font-weight: 600;
              line-height: 102%;
              margin-right: 15px;
            }
          }

          &:last-of-type {
            margin-top: 14px;
          }
        }
      }
    }

  }

  .middle-container {
    position: relative;
    padding-top: 30px;
    max-width: calc(1134px + 48px);

    &:before {
      position: absolute;
      top: 0;
      left: 24px;
      right: -1500px;
      content: "";
      border-top: 1px solid #E8E8E8;

      @media (max-width: $tablet) {
        left: 50px;
      }

      @media (max-width: $mobile) {
        left: 16px;
      }
    }

    .map-block {
      display: flex;
      width: 638px;
      margin: 0 auto;
      padding-bottom: 30px;

      @media (max-width: $mobile) {
        width: 100%;
        max-width: 638px;
      }


      img {
        display: flex;
      }
    }

    .photo-wrp {
      padding-top: 30px;
      border-top: 1px solid #E8E8E8;
      margin: -15px;
      display: flex;
      flex-wrap: wrap;
      justify-content: center;

      @media (max-width: $tablet) {
        margin: -10px -15px;
        justify-content: flex-start;
      }

      @media (max-width: $mobile) {
        margin: 0;
        display: block;
      }

      .item {
        flex: 0 calc(33.333% - 30px);
        width: calc(33.333% - 30px);
        margin: 15px;

        @media (max-width: $tablet) {
          flex: 0 calc(50% - 20px);
          width: calc(50% - 20px);
          margin: 15px 10px;
        }

        @media (max-width: $mobile) {
          width: 100%;
          margin: 15px 0 0;
        }

        &:first-child {
          @media (max-width: $mobile) {
            margin-top: 0;
          }
        }

        img {
          display: flex;
          border-radius: 5px;
        }

        .item-title {
          display: block;
          margin-top: 8px;
          color: #242625;
          font-family: $font-wix;
          font-size: 18px;
          font-style: normal;
          font-weight: 600;
          line-height: 100%;

          i {
            font-style: normal;
            font-size: 16px;
            font-weight: 400;
            display: block;
          }
        }
      }
    }

    .content-wrapper {
      display: flex;
      align-items: center;
      max-width: 940px;
      margin: 30px auto 47px;

      @media (max-width: $mobile) {
        margin-top: 70px;
        display: block;
      }

      .left-block {
        flex: 0 567px;
        width: 567px;
        margin-right: 21px;

        @media (max-width: $tablet) {
          margin-right: 10px;
          flex: 0 100%;
          width: 100%;
        }

        p {
          margin: 0;

          &:first-child {
            &:first-letter {
              initial-letter: 3;
              font-size: 75px;
              font-weight: $font-wix;
              font-weight: 600;
              line-height: 102%;
              margin-right: 15px;
            }
          }

          &:last-of-type {
            margin-top: 14px;
          }
        }
      }

      .right-block {
        flex: 1 0 auto;

        @media (max-width: $tablet) {
          flex: 1 0 302px;
          display: flex;
        }

        @media (max-width: $mobile) {
          width: 100%;
          margin-top: 15px;
        }

        img {
          width: 285px;
          display: block;
          margin: 0 auto;
        }
      }
    }

    .sh-main-img {
      display: flex;
      max-width: 940px;
      margin: 0 auto 50px;

      @media (max-width: $mobile) {
        margin-bottom: 40px;
      }
    }

    .info-paragraph {
      padding: 16px 0;
      margin: 0 auto;
      border-top: 1px solid #E8E8E8;
      border-bottom: 1px solid #E8E8E8;
      max-width: 940px;
    }
  }
}

.not-found-section {
  position: relative;
  padding: 245px 0 180px;
  display: flex;
  align-items: center;
  justify-content: center;

  @media (max-width: $tablet) {
    padding-bottom: 100px;
  }

  .not-found-content {
    position: relative;
    text-align: center;

    &:before {
      position: absolute;
      top: -210px;
      left: 50%;
      content: "";
      background: url(../img/sh-main-img.png) 0 0 no-repeat;
      width: 306px;
      background-size: 100% auto;
      height: 254px;
      transform: translateX(-50%);
      z-index: -1;
    }

    .title {
      display: flex;
      justify-content: center;
      color: #1B532B;
      font-family: $font-wix;
      font-size: 100px;
      font-style: normal;
      font-weight: 700;
      line-height: 101.953%; /* 101.953px */
    }

    p {
      position: relative;
      display: block;
      margin: -7px 0 25px;
      color: #242625;
      text-align: center;
      font-size: 18px;
      font-style: normal;
      font-weight: 400;
      line-height: 140%; /* 25.2px */
    }

    .btn {
      width: 172px;
    }
  }
}

.terms-section {
  position: relative;
  padding: 60px 0 100px;

  @media (max-width: $mobile) {
    padding: 26px 0 100px;
  }

  .terms-content {
    max-width: 940px;
    margin: 0 auto;

    @media (max-width: $tablet) {
      max-width: 670px;
    }
  }

  h2,
  .h2 {
    margin-bottom: 28px;
  }

  ul {
    margin: 12px 0;
    list-style-type: none;
    padding: 0;

    li {
      position: relative;
      padding-left: 45px;
      margin-top: 12px;

      @media (max-width: $mobile) {
        padding-left: 35px;
      }

      &:first-child {
        margin-top: 0;
      }

      &:before {
        position: absolute;
        top: 7px;
        left: 24px;
        content: "";
        width: 9px;
        height: 9px;
        border-radius: 100px;
        background: #1B532B;

        @media (max-width: $mobile) {
          left: 14px;
        }
      }
    }
  }

  p {
    padding: 12px 0;
  }
  a {
    color: #1B532B;
    text-decoration: underline;

    &:hover {
      text-decoration: none;
    }
  }
}

.team-section {
  position: relative;
  padding: 60px 0 100px;

  @media (max-width: $mobile) {
    padding: 26px 0 100px;
  }

  .top-block {
    display: flex;

    @media (max-width: $tablet) {
      display: block;
      max-width: 670px;
      margin: 0 auto;
    }

    .left-block {
      flex: 0 calc(100% - 427px - 44px);
      width: calc(100% - 427px - 44px);
      margin-right: 44px;

      @media (max-width: $tablet) {
        width: 100%;
        margin: 0 0 30px;
        text-align: center;
      }

      @media (max-width: $mobile) {
        margin-bottom: 10px;
      }

      h2,
      .h2 {
        margin-bottom: 12px;
      }

      p {
        margin: 0 0 26px;
      }

      img {
        @media (max-width: $mobile) {
          width: auto;
          height: 118px;
        }
      }
    }

    .right-block {
      flex: 1 0 427px;
      width: 427px;

      @media (max-width: $tablet) {
        width: 100%;
        text-align: center;
      }
    }
  }

  .team-member {
    margin-top: 50px;
    padding-bottom: 60px;

    @media (max-width: $tablet) {
      margin-top: 94px;
    }

    @media (max-width: $mobile) {
      margin-top: 80px;
    }

    h2,
    .h2 {
      text-align: center;
      margin-bottom: 34px;
    }

    .wrapper {
      display: flex;
      flex-wrap: wrap;
      margin: -22.5px;

      @media (max-width: $tablet) {
        margin: -20px -12px;
      }

      @media (max-width: $mobile) {
        display: block;
        margin: 0;
      }

      .item {
        flex: 0 calc(33.333% - 45px);
        width: calc(33.333% - 45px);
        margin: 22.5px;

        @media (max-width: $tablet) {
          flex: 0 calc(50% - 24px);
          width: calc(50% - 24px);
          margin: 20px 12px;
        }

        @media (max-width: $mobile) {
          width: 100%;
          margin: 60px auto 0;
          max-width: 370px;
        }

        &:first-child {
          @media (max-width: $mobile) {
            margin-top: 0;
          }
        }

        &:hover {
          .info-block {
            .name {
              color: #158133;
            }
          }
        }

        .img-block {
          display: flex;
          border-radius: 5px;

          img {
            display: block;
          }
        }

        .info-block {
          margin-top: 14px;
          display: block;

          .name {
            display: block;
            color: #242625;
            font-family: $font-wix;
            font-size: 24px;
            font-style: normal;
            font-weight: 700;
            line-height: 101.953%; /* 24.469px */
            margin-bottom: 8px;
            transition: all .3s
          }

          p {
            display: block;
            margin: 0;
            font-size: 15px;
            line-height: 140%;
            color: #242625 !important;
          }
        }
      }
    }
  }

  .partners-support-block {
    padding-top: 60px;
    border-top: 1px solid #E8E8E8;

    .wrapper {
      display: flex;
      margin-bottom: 70px;

      @media (max-width: $tablet) {
        display: block;
      }

      .left-block {
        flex: 1 0 500px;
        width: 500px;

        @media (max-width: $tablet) {
          width: 100%;
          margin-bottom: 20px;
        }
