      background: linear-gradient(270deg, #F9F8F4 0%, rgba(249, 248, 244, 0.00) 100%);

      @media (max-width: $mobile) {
        display: none;
      }
    }

    &:after {
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      content: "";
      display: none;
      width: 28px;
      background: linear-gradient(90deg, #F9F8F4 0%, rgba(249, 248, 244, 0.00) 100%);

      @media (max-width: $mobile) {
