* {
  box-sizing: border-box;
}

html,
body {
  height: 100%;
}

a {
  outline: none;
  -webkit-transition: all .3s;
  -moz-transition: all .3s;
  transition: all .3s;
  opacity: 1;

  &:hover {
    outline: none !important;
  }

  &:focus {
    outline: none !important;
  }

}

*:focus {
  outline: none;
}

body {
  position: relative;
  font-family: $base-font-family;
  font-size: $base-font-size;
  line-height: $base-line-height;
  font-weight: $base-font-weight;
  color: $base-color;
  -webkit-font-smoothing: antialiased;
  background: #F9F8F4;

  @media (max-width: $mobile) {
    font-size: 15px;
  }
}

a {
  color: $base-color;
  text-decoration: none;
  
  &:hover,
  &:focus {
    color: #1B532B;
    text-decoration: none;
  }
}

input[type="text"],
input[type="email"],
input[type="password"],
textarea {
  -webkit-appearance: none;
  background-clip: padding-box;
}

//clearfix
.clearfix {
  &:before,
  &:after {
    content: " ";
    display: table;
  }

  &:after {
    clear: both;
  }
}


.container {
  padding-left: 24px;
  padding-right: 24px;
  max-width: calc(1200px + 48px);
  margin: 0 auto;

  @media (max-width: $mobile) {
    //max-width: calc(1140px + 40px);
    padding-left: 16px;
    padding-right: 16px;
  }
}

.text-center {
  text-align: center;
}

.text-left {
  text-align: left;
}

.text-right {
  text-align: right;
}


input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
  -webkit-appearance: none; 
  margin: 0; 
}

h1,
.h1 {
  display: block;
  font-style: normal;
  margin: 0;

  color: #242625;
  font-family: $font-wix;
  font-size: 55px;
  font-weight: 700;
  line-height: 101.953%;

  i {
    font-style: normal;
    font-weight: 400;
  }

  @media (max-width: $mobile) {
    font-size: 40px;
  }
}

h2,
.h2 {
  display: block;
  margin: 0;
  color: #242625;
  font-family: $font-wix;
  font-size: 42px;
  font-style: normal;
  font-weight: 700;
  line-height: 101.953%;

  @media (max-width: $mobile) {
    font-size: 36px;
  }

  i {
    font-style: normal;
    font-weight: 400;
  }
}

h3,
.h3 {
  display: block;
  margin: 0;
  color: #242625;
  font-family: $font-wix;
  font-size: 32px;
  font-style: normal;
  font-weight: 700;
  line-height: 120%;

  @media (max-width: $mobile) {
    font-size: 24px;
  }

  i {
    font-style: normal;
    font-weight: 400;
  }
}

h4,
.h4 {
  display: block;
  margin: 0;
  color: #242625;
  font-family: $font-wix;
  font-size: 28px;
  font-style: normal;
  font-weight: 700;
  line-height: 101.953%; /* 28.547px */

  @media (max-width: $mobile) {
    font-size: 18px;
  }

  i {
    font-style: normal;
    font-weight: 400;
  }
}