.show-menu {
  .header {
    .wrapper {
      .main-navigation {
        @media (max-width: $tablet) {
          top: calc(100% + 6px);
          opacity: 1;
          transform: scale(1);
          visibility: visible;
        }
      }
    }
  }
}
.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background: #fff;
  z-index: 10;

  @media (max-width: $mobile) {
    border-bottom: 1px solid #CFD2E2
  }

  &.fixed {
    &:before {
      transform: translateY(0);
    }

    .inner-wrapper {
      .right-block {
        .header-link {
          color: #fff;

          &.chat,
          &.phone {
            &:before {
              background: #98A9E8;
            }
          }

          &.order {
            color: #fff;

            &:before {
              background-color: #FFB30E;
            }
          }
        }
        
        .login-button {
          background: #7C8FCE;
          
          &:before {
            background-color: #fff;
          }
        }
      }
    }
  }

  .wrapper {
    position: relative;
    display: flex;
    height: 62px;
    align-items: center;

    .logo {
      position: relative;
      display: inline-flex;
      align-items: center;

      &:hover {
        .logo-part-1 {
          transform: scale(1.25);
        }
      }

      .logo-part-1 {
        transition: all 1s ease .2s;

        @media (max-width: $mobile) {
          width: 32px;
        }
      }

      .logo-part-2 {
        margin-left: 5px;

        @media (max-width: $mobile) {
          width: 115px;
        }
      }
    }

    .main-navigation {
      margin-left: auto;

      @media (max-width: $tablet) {
        position: absolute;
        //top: calc(100% + 6px);
        right: 0;
        border-radius: 6px;
        border: 1px solid #e8e8e8;
        background: #fff;
        width: 230px;
        padding: 40px 20px 40px 30px;
        top: 0;
        transform: scale(0.7);
        opacity: 0;
        visibility: hidden;
        transition: all .3s ease .2s;
      }

      .nav-list {
        @include reset-list;
        display: flex;

        @media (max-width: $tablet) {
          display: block;
        }

        li {
          margin-left: 26px;

          @media (max-width: $tablet) {
            width: 100%;
            margin: 38px 0 0;
          }

          &:first-child {
            @media (max-width: $tablet) {
              margin-top: 0;
            }
          }
        }

        a {
          display: flex;
          font-size: 13px;
          font-weight: 500;

          @media (max-width: $tablet) {
            font-size: 16px;
            font-weight: 500;
            line-height: 1em;
          }
        }
      }
    }

    .lang-switcher {
      display: flex;
      width: 78px;
      height: 33px;
      border-radius: 7px;
      background: #F3F3F3;
      margin-left: 42px;
      align-items: center;
      justify-content: center;

      @media (max-width: $tablet) {
        margin-left: auto;
      }

      @media (max-width: $mobile) {
        width: 101px;
        height: 40px;
      }

      .lang-link {
        display: inline-flex;
        align-items: center;
        justify-content: center;
        width: 34px;
        height: 23px;
        font-size: 13px;
        font-weight: 500;
        color: #626c67;
        transition: all .3s;

        @media (max-width: $mobile) {
          width: 46px;
          height: 30px;
        }

        &:hover,
        &.active {
          border-radius: var(--rounded, 4px);
          background: #FFF;
          box-shadow: 0px 2px 2.6px 0px rgba(34, 47, 40, 0.20);
          color: #242625;
        }

        &.active {
          &:hover {
            box-shadow: none;
            //background: none;
          }
        }
      }
    }

    .menu-burger {
      position: relative;
      flex:  0 33px;
      width: 33px;
      display: none;
      height: 33px;
      margin-left: 12px;
      border-radius: 7px;
      background: #f3f3f3;

      @media (max-width: $tablet) {
        display: flex;
      }

      @media (max-width: $mobile) {
        width: 40px;
        height: 40px;
        flex: 0 40px;
      }

      &:hover {
        span {
          background: #158133;
        }
      }

      &.active {
        span {
          &:nth-child(1) {
            transition-delay: 75ms;
            transform: translate3d(0,6px,0) rotate(135deg);

            @media (max-width: $mobile) {
              transform: translate3d(0,4px,0) rotate(135deg);

            }
          }

          &:nth-child(2) {
            transition-delay: 0s;
            opacity: 0;
          }

          &:nth-child(3) {
            transition-delay: 75ms;
            transform: translate3d(0,-6px,0) rotate(-315deg);
          }
        }
      }

      span {
        position: absolute;
        top: 0;
        right: 7px;
        width: 17px;
        background: #242625;
        height: 2px;
        transition: all .3s cubic-bezier(.68,-.55,.265,1.55);
        border-radius: 5px;

        @media (max-width: $mobile) {
          right: 11px;
        }

        &:nth-child(1) {
          top: 9px;
          transition-property: all;
          transition-timing-function: cubic-bezier(.68,-.55,.265,1.55);
          transition-duration: .275s;

          @media (max-width: $mobile) {
            top: 14px;
          }
        }

        &:nth-child(2) {
          top: 15px;
          transition: opacity .125s ease .275s;

          @media (max-width: $mobile) {
            top: 19px;
          }
        }

        &:nth-child(3) {
          top: 21px;
          transition: all .275s cubic-bezier(.68,-.55,.265,1.55);

          @media (max-width: $mobile) {
            top: 24px;
          }
        }
      }
    }
  }
}