.footer {
  .logos-footer {
    border-top: 1px solid #E1E6F0;

    .wrapper {
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 40px 0;

      @media (max-width: $mobile) {
        //justify-content: space-between;

      }

      img {
        display: inline-flex;
        margin: 0 47.5px;

        @media (max-width: $mobile) {
          margin: 0 15px;
        }
      }
    }
  }

  .middle-footer {
    border-top: 1px solid #E1E6F0;
    padding-top: 32px;

    .wrapper {
      display: flex;
      align-items: center;
      justify-content: space-between;

      @media (max-width: $mobile) {
        display: block;
      }

      .logo {
        position: relative;
        display: inline-flex;
        align-items: center;

        &:hover {
          .logo-part-1 {
            transform: scale(1.25);
          }
        }

        .logo-part-1 {
          transition: all 1s ease .2s;

          @media (max-width: $mobile) {
            width: 32px;
          }
        }

        .logo-part-2 {
          margin-left: 5px;

          @media (max-width: $mobile) {
            width: 115px;
          }
        }
      }

      .footer-nav {
        @media (max-width: $mobile) {
          margin-top: 30px;
        }
        .nav-list {
          @include reset-list;
          display: flex;

          @media (max-width: $mobile) {
            display: block;
          }

          li {
            margin-left: 26px;

            @media (max-width: $mobile) {
              margin-left: 0;
              margin-top: 23px;
            }

            &:first-child {
              @media (max-width: $mobile) {
                margin-top: 0;
              }
            }
          }

          a {
            display: flex;
            font-size: 13px;
            font-weight: 500;

            @media (max-width: $mobile) {
              font-size: 16px;
              line-height: 1em;
            }
          }
        }
      }
    }
  }

  .bottom-footer {
    padding: 25px 0 30px;

    @media (max-width: $tablet) {
      padding-top: 40px;
    }

    @media (max-width: $mobile) {
      padding-top: 50px;
      padding-bottom: 40px;
    }

    .wrapper {
      display: flex;
      align-items: center;
      justify-content: space-between;

      @media (max-width: $tablet) {
        flex-wrap: wrap;
      }

      .powered-block {
        order: 2;
        display: flex;
        align-items: flex-end;

        @media (max-width: $tablet) {
          order: 1;
          margin-bottom: 40px;
          width: 100%;
          justify-content: flex-end;
        }

        @media (max-width: $mobile) {
          justify-content: flex-start;
        }

        .title {
          display: inline-flex;
          font-size: 14px;
          font-weight: 400;
          color: #414744;
          margin-right: 22px;
          line-height: 1em;
        }

        .powered-link {
          display: inline-flex;
          margin-right: 13px;
          transition: all 1s ease .2s;

          &:hover {
            transform: scale(1.1);
          }

          &:last-of-type {
            margin-right: 0;
          }
        }
      }

      .copyright-block {
        display: flex;
        align-items: flex-end;

        @media (max-width: $tablet) {
          order: 2;
        }

        @media (max-width: $mobile) {
          flex-wrap: wrap;
        }

        .copyright-title {
          display: inline-flex;
          color: #A3B0AD;
          font-size: 14px;
          font-style: normal;
          font-weight: 400;
          margin-right: 41px;
          line-height: 1em;

          @media (max-width: $mobile) {
            margin-bottom: 12px;
          }
        }

        .legal-link {
          display: inline-flex;
          color: #A3B0AD;
          font-size: 14px;
          font-style: normal;
          font-weight: 400;
          margin-right: 24px;
          line-height: 1em;

          &:last-of-type {
            margin-right: 0;
          }

          &:hover {
            color: #1B532B;
          }
        }
      }
    }
  }
}