.btn {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  transition: all .5s;
  background: none;
  border: 0;

  font-size: 15px;
  font-weight: 500;
  line-height: 1em;
  height: 44px;
  border-radius: 6px;

  @media (max-width: $desk) {
    padding: 0 15px;
  }
  
  &.green {
    background: #1B532B;
    color: #fff;
    
    &:hover {
      background: #158133;
    }
  }
  
  &.white {
    border-radius: 6px;
    background: #FFF;
    font-size: 20px;
    color: #1B532B;

    &:hover {
      color: #158133;
    }
  }

}

.form-title {
	display: block;
	font-size: 14px;
	font-weight: 500;
	line-height: 140%;
	margin-bottom: 5px;
}

.form-select {
	width: 100%;
	display: block;
	height: 47px;
	background: #fff url(../img/arrow_down.png) 95% 50% no-repeat;
	padding: 0 25px;
	font-size: 16px;
	font-weight: 300;
	color: #1f1d34;
	border: 1px solid rgba(#6496ed, 0.3);
	transition: all .3s;
	appearance: none;
}

.form-control {
	width: 100%;
	display: block;
	height: 44px;
	padding: 0 14px;
	font-size: 14px;
	font-weight: 400;
	color: #1f1d34;
    border-radius: 5px;
    border: 1px solid #e1e6f0;

  background: #fff;
  box-shadow: 0px 0px 5px 0px rgba(195, 201, 206, 0.30);
	transition: all .3s;

  &:focus,
  &:active {
      border-color: #1B532B !important;
  }

  &::-webkit-input-placeholder {
      color: #BDC1BE;
  }
  &::-moz-placeholder {
      color: #BDC1BE;
  }
  &:-ms-input-placeholder {
      color: #BDC1BE;
  }
  &:-moz-placeholder {
      color: #BDC1BE;
  }
}

.form-area {
  width: 100%;
  display: block;
  padding: 11px 14px !important;
  font-size: 14px;
  font-weight: 400;
  color: #1f1d34;
  border-radius: 5px;
  border: 1px solid #e1e6f0;

  background: #fff;
  box-shadow: 0px 0px 5px 0px rgba(195, 201, 206, 0.30);
  transition: all .3s;

  &:focus,
  &:active {
    border-color: #1B532B !important;
  }

  &::-webkit-input-placeholder {
    color: #BDC1BE;
  }
  &::-moz-placeholder {
    color: #BDC1BE;
  }
  &:-ms-input-placeholder {
    color: #BDC1BE;
  }
  &:-moz-placeholder {
    color: #BDC1BE;
  }
	resize: none;
}

.nano {
  height: 100%;

  position: relative;
  width: 100%;
}

.nano .nano-content {
  bottom: 0;
  left: 0;
  overflow: scroll;
  overflow-x: hidden;
  padding: 15px 5%;
  position: absolute;
  right: 0;
  top: 0
}


.nano .nano-content::-webkit-scrollbar {
  display: none
}

.nano>.nano-pane {
  bottom: 4px;
  opacity: 1;
  position: absolute;
  right: 0;
  top: 4px;
  transition: .2s;
  width: 10px
}

.nano>.nano-pane>.nano-slider {
  background: #1B532B;
  margin: 0 4px;
  position: relative
}

.has-scrollbar>.nano-content::-webkit-scrollbar {
  display: block
}


.pagination-block {
  display: flex;
  height: 30px;
  max-width: 254px;
  align-items: center;
  margin: 30px auto 0;

  @media (max-width: $mobile) {
    height: 60px;
    margin-top: 30px;
  }

  .next-btn,
  .prev-btn {
    position: relative;
    flex: 1 0 30px;
    height: 30px;
    display: block;
    border: 1px solid #1B532B;
    border-radius: 100px;
    background: none;
    transition: all .3s;

    @media (max-width: $mobile) {
      flex: 1 0 30px;
      height: 30px;
    }

    &:hover {
      background: #158133;
    }

    &.disabled,
    &:disabled {
      border-color: #E2E6E7;
      pointer-events: none;

      &:before {
        background-color: #E2E6E7;
      }
    }
  }

  .prev-btn {
    &:before {
      position: absolute;
      top: 50%;
      left: 50%;
      content: "";
      mask: url(../img/arrow-right.svg) no-repeat 50% 50%;
      -webkit-mask-size: cover;
      mask-size: cover;
      background-color: #1B532B;
      width: 16px;
      height: 16px;
      margin-top: -8px;
      margin-left: -8px;
      transition: all .3s;
      transform: rotate(180deg);

    }

    &:hover {
      &:before {
        background-color: #fff;
      }
    }
  }

  .next-btn {
    &:before {
      position: absolute;
      top: 50%;
      left: 50%;
      content: "";
      mask: url(../img/arrow-right.svg) no-repeat 50% 50%;
      -webkit-mask-size: cover;
      mask-size: cover;
      background-color: #1B532B;
      width: 16px;
      height: 16px;
      margin-top: -8px;
      margin-left: -8px;
      transition: all .3s;
    }

    &:hover {
      &:before {
        background-color: #fff;
      }
    }
  }

  .inner-pag-block {
    flex: 0 100%;

    ul {
      display: flex;
      justify-content: center;
      @include reset-list;
      height: 100%;
      align-items: center;

      @media (max-width: $tablet) {
        height: 60px;
      }

      li {
        position: relative;
        width: 24px;
        text-align: center;
        height: 30px;
        margin: 0 5px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 100px;

        @media (max-width: $mobile) {
          width: auto;
          flex: 0 auto;
          min-width: 40px;
          margin: 0;
          height: 40px;
        }

        &.active {
        	background: #f7f9fe;
          .pag-link {
            color: #6496ed;
          }
        }

      }

      .pag-link {
        color: #242625;
        font-size: 16px;
        font-weight: 500;
        line-height: 130%;

        @media (max-width: $mobile) {
          font-size: 15px;
        }

        &:hover {
          color: #1B532B;
        }
      }

      .dots {
        color: #5D6967;
      }
    }
  }
}

.f_group .jq-selectbox__select-text {
  // display: flex;
  // align-items: center;
  height: 44px;
  border-radius: 8px;
  border: 1px solid #e1e6f0;
  background: #FFF;
  box-shadow: 0px 0px 5px 0px rgba(195, 201, 206, 0.30);
  padding: 10px 14px;
  font-size: 14px;
  color: #242625;
  transition: all .3s;
  appearance: none;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  display: block;
  text-align: left;
  cursor: pointer;

  &:hover {
    border: 1px solid #1B532B;
  }

  &.placeholder {
    color: #BDC1BE;
  }
}

.f_group .jq-selectbox {
    width: 100%;
    z-index: auto
}

.f_group .jq-selectbox .jq-selectbox__not-found {
    position: relative;
    z-index: 9;
    padding: 0 36px 11px 0;
    word-wrap: break-word;
    background: #fff
}

.f_group .jq-selectbox .nano>.nano-pane {
    right: 3px;
    border: none;
    width: 6px;
    background: none;
    height: calc(100% - 8px);
    top: 4px;
}

.f_group .jq-selectbox .nano>.nano-pane>.nano-slider {
    margin: 0;
    width: 100%;
    background: #1B532B;
}

.f_group .jq-selectbox ul .disabled {
    display: none
}

.f_group .jq-selectbox select {
    width: 100%;
    height: 100%
}

.f_group .jq-selectbox__dropdown {
    position: absolute;
    width: 100%;
  border-radius: 0 0 8px 8px;
  border: 1px solid #1B532B;
  border-top: 1px solid #E1E6F0;
  background: #FFF;
  overflow: hidden;
  box-shadow: 0px 0px 5px 0px rgba(195, 201, 206, 0.30);
}

.f_group .jq-selectbox__dropdown ul {
    background: #fff;
    @include reset-list;
}

.f_group .jq-selectbox__dropdown ul.nano-content {
    padding-right: 35px
}

.f_group .jq-selectbox__dropdown ul li {
    cursor: pointer;
    transition: all .3s;
    min-height: 52px;
    padding: 7px 14px;
  font-size: 14px;
  color: #242625;
  line-height: 1.4em;
  display: block;
  word-break: break-word;
  display: flex;
  align-items: center;
}

.f_group .jq-selectbox__dropdown ul li.selected,
.f_group .jq-selectbox__dropdown ul li:hover {
  background: #DFFFE8;
}

.f_group .jq-selectbox__trigger {
    position: absolute;
    top: 0;
    right: 0;
    width: 35px;
    height: 100%;
    transition: .3s all;
    cursor: pointer
}

.f_group .jq-selectbox__trigger .jq-selectbox__trigger-arrow {
    position: absolute;
    width: 8px;
    height: 8px;
    border: 2px solid #5D6967;
    border-top: none;
    border-left: none;
    left: calc(50% - 4px);
    top: calc(50% - 4px);
    transition: .3s all;
    transform-origin: 62%;
    transform: rotate(45deg);
}

.f_group .opened .jq-selectbox__trigger-arrow {
    transform: rotate(-135deg)
}
.f_group .opened .jq-selectbox__select-text {
  border: 1px solid #1B532B;
  border-radius: 8px 8px 0 0;
  border-bottom: 0;
}

.f_group .dropdown .jq-selectbox__dropdown {
}

.f_group .dropup .jq-selectbox__select-text {
  border-bottom: 1px solid #1B532B;
  border-top: 0;
  border-radius: 0 0 8px 8px;
}
.f_group .dropup .jq-selectbox__dropdown {
  border-top: 1px solid #1B532B;
  border-bottom: 1px solid #e1e6f0;
  border-radius: 8px 8px 0 0;
}

.custom-file-upload {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
  border: 1px dashed #CED2DB;
  background: #F8F8F8;
  box-shadow: 0px 0px 5px 0px rgba(195, 201, 206, 0.30);
  height: 57px;
  font-size: 14px;
  font-weight: 400;

  b {
    font-weight: 600;
    margin: 0 4px;
    color: #1B532B;
  }

}
input[type="checkbox"],
input[type="file"] {
  position: absolute;
  width: 0;
  height: 0;
  opacity: 0;
  visibility: hidden;
}

input[type="checkbox"]:checked + .custom-checkbox {
  &:before {
    background: #1B532B;
    border-color: #1B532B;
  }

  &:after {
    transform: scale(1);
  }
}
.custom-checkbox {
  position: relative;
  display: flex;
  align-items: center;
  font-size: 14px;
  font-weight: 500;
  line-height: 140%;
  cursor: pointer;
  padding-left: 28px;

  &:hover {
    &:before {
      border-color: #1B532B;
    }
  }

  &:before {
    position: absolute;
    top: 50%;
    left: 0;
    content: "";
    width: 18px;
    height: 18px;
    background: #fff;
    margin-top: -10px;
    border: 1px solid #E1E6F0;
    border-radius: 3px;
    transition: all .3s ease .1s;
  }

  &:after {
    position: absolute;
    top: 50%;
    left: 0;
    width: 20px;
    height: 20px;
    content: "";
    background: url(../img/check-icon.svg) 50% 50% no-repeat;
    margin-top: -10px;
    transition: all .3s ease .1s;
    transform: scale(0.6);
  }
}

.fancybox__backdrop {
  background: rgba(#000, 0.8);
}

.fancybox__caption {
  padding: 10px;
  border-radius: 5px;
  background: #F9F8F4;
  margin-top: 5px;
  color: #242625;
  font-family: $font-wix;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 120%; /* 19.2px */

  @media (max-width: $mobile) {
    border-radius: 0;
  }

  i {
    font-weight: 400;
  }
}

.fancybox__nav {
  .f-button {
    width: 56px;
    height: 56px;
    border-radius: 28px;
    background: #f9f8f4;

    @media (max-width: $mobile) {
      width: 40px;
      height: 40px;
    }

    &:before {
      position: absolute;
      top: 50%;
      left: 50%;
      content: "";
      mask: url(../img/modal-arrow.svg) 0 0 no-repeat;
      background-color: #1B532B;
      width: 24px;
      height: 24px;
      margin-top: -12px;
      margin-left: -12px;
      transition: all .3s ease .1s;
    }
    
    &:hover {
      background: #f9f8f4;

      &:before {
        background-color: #000;
        transform: scale(1.2);
      }
    }

    &.is-next {
      &:before {
        transform: rotate(180deg);
      }

      &:hover {
        &:before {
          transform: rotate(180deg) scale(1.2);
        }
      }
    }

    svg {
      display: none;
    }
  }
}

.f-button[data-fancybox-close] {
  background: url(../img/x-close.svg) 50% 50% no-repeat;
  svg {
    display: none;
  }
}