.links-section {
  position: relative;
  padding: 60px 0 100px;

  @media (max-width: $mobile) {
    padding: 26px 0 100px;
  }

  .top-block {
    padding-bottom: 36px;

    h2,
    .h2 {
      margin-bottom: 36px;
      text-align: center;
    }

    .browse-top-title {
      display: flex;
      justify-content: center;
      font-size: 18px;
      margin-bottom: 14px;
      font-family: $font-wix;
    }

    .browse-links-wrp {
      display: flex;
      justify-content: center;

      @media (max-width: $tablet) {
        flex-wrap: wrap;
      }

      @media (max-width: $mobile) {
        flex-wrap: nowrap;
        overflow: hidden;
        overflow-x: auto;
        justify-content: flex-start;
      }

      .anchor-link {
        display: inline-flex;
        align-items: center;
        justify-content: center;
        padding: 0 20px;
        height: 36px;
        border: 1px solid #e1e6f0;
        font-weight: 500;
        margin-left: -1px;
        transition: all .3s ease;

        @media (max-width: $tablet) {
          margin-top: -1px;
        }

        @media (max-width: $mobile) {
          white-space: nowrap;
          margin-top: 0;
        }

        &:first-child {
          @media (max-width: $mobile) {
            margin-left: 0;
          }
        }

        &:hover {
          background: #fff;
          color: inherit;
        }
      }
    }
  }

  .bottom-block {
    border-top: 1px solid #E8E8E8;

    .link-block {
      max-width: 960px;
      margin: 0 auto;

      &:first-child {
        h2,
        .h2 {
          border-top: 0;
          margin-top: 0;
        }
      }

      h2,
      .h2 {
        padding: 22px 0;
        border-top: 1px solid #E8E8E8;
        border-bottom: 1px solid #E8E8E8;
        margin-bottom: 22px;
        text-align: center;
        font-size: 43px;
        margin-top: 22px;

        @media (max-width: $tablet) {
          padding-left: 20px;
          padding-right: 20px;
        }

        @media (max-width: $mobile) {
          font-size: 30px;
        }
      }
    }

    .link-row {
      position: relative;
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      height: 104px;
      padding-left: 120px;
      padding-right: 210px;
      border-radius: 3px;
      border: 1px solid #E1E6F0;
      background: #F9F8F4;
      box-shadow: 0px 0px 5px 0px rgba(195, 201, 206, 0.30);
      font-size: 20px;
      font-family: $font-wix;
      font-weight: 500;
      line-height: 102%;
      transition: all 0s;
      overflow: hidden;
      margin-top: 10px;

      @media (max-width: $mobile) {
        height: 80px;
        padding-left: 14px;
        padding-right: 40px;
        font-size: 15px;
      }

      &:first-child {
        margin-top: 0;
      }

      &:hover {
        border: 1px solid #1B532B;
        background: #1B532B;
        color: #fff;
      }

      &:after,
      &:before {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        content: "";
        //transition: all .1s;
      }

      &:before {
        left: 0;

        @media (max-width: $mobile) {
          display: none;
        }
      }

      &:after {
        right: 0;
        z-index: 0;
      }

      span {
        position: relative;
        z-index:  1;
      }

      i {
        display: block;
        font-style: normal;
        font-size: 14px;
        font-weight: 400;
        width: 100%;

        @media (max-width: $mobile) {
          font-size: 12px;
        }
      }
    }
